import React, { Component } from 'react'

export default class RemainingCharacter extends Component {
    constructor(props) {
        super(props)

        this.state = {
            remaining: 100
        }
    }

    toggleRemainingEvenet = (e) => {
        this.setState({ remaining: 100 - e.target.value.length })
    }

    render() {
        return (
            <div>
                <textarea onKeyUp={this.toggleRemainingEvenet}></textarea>
                <p>Remaining Character : {this.state.remaining}</p>
            </div>
        )
    }
}
