import React, { Component } from 'react'

export default class PasswordCheck extends Component {
    constructor(props) {
        super(props)

        this.state = {
            flag: true
        }
    }

    toggle = () => {
        this.setState({ flag: !this.state.flag })
    }

    render() {
        return (
            <div>
                <input type={this.state.flag ? 'password' : 'textbox'} />
                <input type='checkbox' onClick={this.toggle} /> {this.state.flag ? 'SHOW' : 'HIDE'} Password
            </div>
        )
    }
}





