import React, { Component } from 'react'

export default class EventDemo1 extends Component {
    f1 = () => {
        console.log("I am f1.");
    }

    add = (a, b) => {
        console.log(a + b);
    }
    render() {
        return <>
            <button className='btn btn-primary m-2' onClick={this.f1}>btn-1</button>
            <button className='btn btn-secondary' onClick={() => this.add(2, 3)}>Add</button>
        </>
    }
}
