import React, { useEffect, useState } from 'react'
import client from './client';
import Spinner from 'react-bootstrap/Spinner';
import CardUsersDetails from './CardUsersDetails';
import axios from 'axios';
import { myInterceptor1, requestStartedInterceptor, responseReceivedInterceptor } from '../../utils/MyInterceptor1';


const AxiosExample1 = () => {

    myInterceptor1();
    requestStartedInterceptor();
    responseReceivedInterceptor();

    const [users, setUser] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    const fetchUserDetails = async () => {
        // const response = await client.get('/products')
        // let url = 'https://fakestoreapi.com/products';
        let url = 'https://jsonplaceholder.typicode.com/users';
        let response = await axios.get(url);
        console.log(response);
        const result = response.data;
        console.log(result);
        setUser(result)
        setIsLoading(false)
    }

    useEffect(() => {
        setIsLoading(true)
        fetchUserDetails();
    }, [])

    return <>
        {
            isLoading ?
                <>
                    <Spinner animation="border" variant="primary" />
                    <Spinner animation="border" variant="secondary" />
                    <Spinner animation="border" variant="success" />
                    <Spinner animation="border" variant="danger" />
                    <Spinner animation="border" variant="warning" />
                    <Spinner animation="border" variant="info" />
                    <Spinner animation="border" variant="light" />
                    <Spinner animation="border" variant="dark" />
                    <Spinner animation="grow" variant="primary" />
                    <Spinner animation="grow" variant="secondary" />
                    <Spinner animation="grow" variant="success" />
                    <Spinner animation="grow" variant="danger" />
                    <Spinner animation="grow" variant="warning" />
                    <Spinner animation="grow" variant="info" />
                    <Spinner animation="grow" variant="light" />
                    <Spinner animation="grow" variant="dark" />
                </>
                :
                <CardUsersDetails users={users} />
        }
    </>
}

export default AxiosExample1
