import React, { useEffect, useState } from 'react'
import Spinner from 'react-bootstrap/Spinner';
import axios from 'axios';
import { Card, ListGroup } from 'react-bootstrap';
import { Link, createSearchParams, useNavigate } from 'react-router-dom';


const Users = () => {


    const [users, setUser] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    const navigate = useNavigate();

    const fetchUserDetails = async () => {
        // const response = await client.get('/products')
        // let url = 'https://fakestoreapi.com/products';
        let url = 'https://jsonplaceholder.typicode.com/users';
        let response = await axios.get(url);
        console.log(response);
        const result = response.data;
        console.log(result);
        setUser(result)
        setIsLoading(false)
    }

    useEffect(() => {
        setIsLoading(true)
        fetchUserDetails();
    }, [])

    const goToUserDetails = (name, { city }, email) => {
        debugger

        let obj = {
            pathname: "/userdetailsFilter",
            search: `?${createSearchParams({ name, city, email })}`,
        }
        navigate(obj);
    }

    return <>
        {
            isLoading ?
                <>
                    <Spinner animation="border" variant="primary" />
                    <Spinner animation="border" variant="secondary" />
                    <Spinner animation="border" variant="success" />
                    <Spinner animation="border" variant="danger" />
                    <Spinner animation="border" variant="warning" />
                    <Spinner animation="border" variant="info" />
                    <Spinner animation="border" variant="light" />
                    <Spinner animation="border" variant="dark" />
                    <Spinner animation="grow" variant="primary" />
                    <Spinner animation="grow" variant="secondary" />
                    <Spinner animation="grow" variant="success" />
                    <Spinner animation="grow" variant="danger" />
                    <Spinner animation="grow" variant="warning" />
                    <Spinner animation="grow" variant="info" />
                    <Spinner animation="grow" variant="light" />
                    <Spinner animation="grow" variant="dark" />
                </>
                :
                <div className='row'>
                    {
                        users.map(({ id, name, username, email, ...address }, ind) => {
                            const keys = Object.keys(address);
                            return < div className="container-fluid-inline col-sm-3" key={`card_${ind}`}>
                                < Card style={{ width: '20rem' }} className='m-2 p-5'>
                                    <Card.Img variant="top" src="https://upload.wikimedia.org/wikipedia/commons/thumb/1/12/User_icon_2.svg/1200px-User_icon_2.svg.png" width={150} height={200} />
                                    <Card.Body>
                                        <Card.Title className='text-truncate'>{id}</Card.Title>
                                        <Card.Text className='text-truncate'>
                                            {name}
                                        </Card.Text>
                                    </Card.Body>
                                    <ListGroup className="list-group-flush">
                                        <ListGroup.Item>{username}</ListGroup.Item>
                                        <ListGroup.Item>{email}</ListGroup.Item>
                                    </ListGroup>
                                    <Card.Body>
                                        <Card.Link href="#">Card Link</Card.Link>
                                        <Card.Link href="#">Another Link</Card.Link>
                                        {
                                            keys.map((key, ind) => {
                                                return <Card.Link href="#">address["key"]</Card.Link>
                                            })
                                        } <br />
                                        <button onClick={() => goToUserDetails(name, address, email)} className='bg bg-secondary'><Link className='text-center' to={`https://jsonplaceholder.typicode.com/users`}>View User</Link></button>
                                    </Card.Body>
                                </Card >
                            </div>
                        })
                    }
                </div>
        }
    </>
}

export default Users
