import React, { useEffect, useState } from 'react'
import { Table } from 'react-bootstrap';
import AddJsonEmployee from './AddJsonEmployee';

const EmployeeJsonCrud = () => {
    const [isLoading, setIsLoading] = useState(false);
    const [employee, setEmployee] = useState([]);

    const fetchEmployee = async () => {
        const response = await fetch("http://localhost:4000/employee");
        const result = await response.json();
        console.log(result)
        setEmployee(result);
    }

    useEffect(() => {
        setIsLoading(true);
        fetchEmployee();
    }, [])

    return <>
        <p>::: Employee Data :::</p>
        <div className='row'>
            <div className='col-8'>
                {
                    < Table striped bordered hover>
                        <thead>
                            <tr>
                                <th>#ID</th>
                                <th>NAME</th>
                                <th>USERNAME</th>
                                <th>EMAIL</th>
                                {/* <th>ADDRESS</th> */}
                            </tr>
                        </thead>
                        <tbody>
                            {
                                employee.map(({ id, name, username, email, address }, ind) => {
                                    return <tr><td>{id}</td>
                                        <td>{name}</td>
                                        <td>{username}</td>
                                        <td>{email}</td>
                                        {/* <td></td> */}
                                    </tr>
                                })
                            }
                        </tbody>
                    </Table >
                }

            </div>
            <div col-4>
                <AddJsonEmployee />
            </div>
        </div>
    </>
}

export default EmployeeJsonCrud
