import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import { Link } from 'react-router-dom';

function CardUsersDetails({ users }) {
    return <div className='row'>
        {
            users.map(({ id, name, username, email, ...address }, ind) => {
                const keys = Object.keys(address);
                return < div className="container-fluid-inline col-sm-3" key={`card_${ind}`}>
                    < Card style={{ width: '20rem' }} className='m-2 p-5'>
                        <Card.Img variant="top" src="https://upload.wikimedia.org/wikipedia/commons/thumb/1/12/User_icon_2.svg/1200px-User_icon_2.svg.png" width={150} height={200} />
                        <Card.Body>
                            <Card.Title className='text-truncate'>{id}</Card.Title>
                            <Card.Text className='text-truncate'>
                                {name}
                            </Card.Text>
                        </Card.Body>
                        <ListGroup className="list-group-flush">
                            <ListGroup.Item>{username}</ListGroup.Item>
                            <ListGroup.Item>{email}</ListGroup.Item>
                        </ListGroup>
                        <Card.Body>
                            <Card.Link href="#">Card Link</Card.Link>
                            <Card.Link href="#">Another Link</Card.Link>
                            {
                                keys.map((key, ind) => {
                                    return <Card.Link href="#">address["key"]</Card.Link>
                                })
                            } <br />
                            <button className='bg bg-secondary'><Link className='text-center' to={`https://jsonplaceholder.typicode.com/users`}>View User</Link></button>
                        </Card.Body>
                    </Card >
                </div>
            })
        }
    </div>
}

export default CardUsersDetails;