import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import { Link } from 'react-router-dom';

function CardProductsDetails({ product }) {
    return <div className='row'>
        {
            product.map(({ id, title, price, description, category, image, rating }, ind) => {
                return < div className="container-fluid-inline col-sm-3" key={`card_${ind}`}>
                    < Card style={{ width: '20rem' }} className='m-2 p-5'>
                        <Card.Img variant="top" src={image} width={200} height={400} />
                        <Card.Body>
                            <Card.Title className='text-truncate'>{title}</Card.Title>
                            <Card.Text className='text-truncate'>
                                {description}
                            </Card.Text>
                        </Card.Body>
                        <ListGroup className="list-group-flush">
                            <ListGroup.Item>{category}</ListGroup.Item>
                        </ListGroup>
                        <Card.Body>
                            <Card.Link href="#">Card Link</Card.Link>
                            <Card.Link href="#">Another Link</Card.Link>
                            <br />
                            <button className='btn btn-warning'>
                                <Link to={`http://localhost:3000/products/${id}`} >View Details</Link>
                            </button>
                        </Card.Body>
                    </Card >
                </div>
            })
        }
    </div >
}

export default CardProductsDetails;