import React, { Component } from 'react'

export default class CounterDemo1 extends Component {
    counter = 0;
    btnClick = () => {
        this.counter = this.counter + 1;
        this.forceUpdate();
        console.log(`This is counter value : ${this.counter}`)
    }
    render() {
        return (
            <div>
                <h2>This is counter demo 1 class component</h2>
                <hr />
                <div>Counter Value is {this.counter}</div>
                <p>
                    <button onClick={this.btnClick}>Increment</button>
                </p>
            </div>
        )
    }
}
