import React from 'react'
import { Link, useNavigate } from 'react-router-dom'

const About = () => {
    const navigate = useNavigate();

    const doSomethingAndNavigateToHome = () => {
        console.log("Do Something....")
        navigate("/home")
    }
    return <>
        <h1>This is About Us Component</h1>
        <a href='/home'>Go to Home Page</a>
        <Link to="/home" className='m-2'>go to home page</Link>
        <button className='m-2' onClick={doSomethingAndNavigateToHome}>Do Something and Navigate To Home Fn Call.</button>
        <button className='m-2' onClick={() => navigate(-1)}>Go Back</button>
        <button className='m-2' onClick={() => navigate(1)}>Go Next</button>
    </>
}

export default About
