import React, { useEffect, useState } from 'react'
import { Card, ListGroup } from 'react-bootstrap';
import { useSearchParams } from 'react-router-dom'

const UserDetails = () => {
    const [searchParams] = useSearchParams();
    const [users, setUser] = useState({});
    useEffect(() => {
        const currentParams = Object.fromEntries([...searchParams]);
        console.log(currentParams);
        console.log(searchParams.get("name"), searchParams.get("price"));
        setUser({
            name: searchParams.get("name"),
            email: searchParams.get("email"),
            city: searchParams.get("city")
        })
    }, [searchParams])
    return <>
        < Card style={{ width: '20rem' }} className='m-2 p-5'>
            <Card.Img variant="top" src="https://upload.wikimedia.org/wikipedia/commons/thumb/1/12/User_icon_2.svg/1200px-User_icon_2.svg.png" width={150} height={200} />
            <Card.Body>
                <Card.Title className='text-truncate'>{users?.id}</Card.Title>
                <Card.Text className='text-truncate'>
                    {users?.name}
                </Card.Text>
            </Card.Body>
            <ListGroup className="list-group-flush">
                <ListGroup.Item>{users?.username}</ListGroup.Item>
                <ListGroup.Item>{users?.email}</ListGroup.Item>
                <ListGroup.Item>{users?.city}</ListGroup.Item>
            </ListGroup>
            <Card.Body>
                <Card.Link href="#">Card Link</Card.Link>
                <Card.Link href="#">Another Link</Card.Link>
                <br />
            </Card.Body>
        </Card >
    </>
}

export default UserDetails
