import React from 'react'
import { Link, Outlet } from 'react-router-dom'

const Career = () => {
    return (
        <div className='text-center my-3'>
            This is Career Component. <br />
            <Link to="contract">Contract Job</Link> <br />
            <Link to="permanent">Permanent Job</Link>
            <Outlet />
        </div>
    )
}

export default Career
